.script-viewer {
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
}
.all-conversations {
  max-width: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.pagination {
  -ms-flex-item-align: end;
      align-self: flex-end;
}
.single-conversation {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border: 1px solid #2e88c4;
  width: 300px;
  min-height: 400px;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.conversation-title {
  -ms-flex-item-align: center;
      align-self: center;
}
.line {
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 0.5em;
}
.active.line {
  border: 2px solid #ff2f56 !important;
}
.line.left {
  background-color: #92d1fb;
  border: 2px solid #92d1fb;
  margin-right: 2em;
}
.line.right {
  background-color: #d8d8d8;
  border: 2px solid #d8d8d8;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin-left: 2em;
}
.controls {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-item-align: center;
      align-self: center;
}
button {
  border: 0;
  height: 30px;
  min-width: 80px;
  border-radius: 4px;
  margin-right: 10px;
  margin-left: 10px;
  text-transform: uppercase;
}
button.primary, button.play, button.next {
  background-color: #2086ca;
  color: white;
}
.sound-sprite {
  -ms-flex-item-align: center;
      align-self: center;
}



